
/* 
   @WebSocketMessage 
*/
export class FilteredEventsSocketServiceOpen {
 
    // Register for push events for this account id 
    public accountId: string;
 
    // Filter events by the provided list of sensor Ids 
    public sensorId: string[];
 
    // Filter events by the provided list of external sensor Ids 
    public extSensorId: string[];
 
    constructor(accountId?: string, sensorId?: string[], extSensorId?: string[]) { 
        this.accountId = accountId;
        this.sensorId = sensorId;
        this.extSensorId = extSensorId;
    }

}


